import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserHttpService } from '../user-http.service';
import { ToastrService  } from 'ngx-toastr';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit,OnDestroy {


  EMAIL_PATTERN = "^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@"
  + "[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$";

  incorrect:string='';
  msg: any;

  constructor(private routes:Router,private service:UserHttpService,private toastr:ToastrService) { }

  ngOnDestroy(): void {
    document.body.classList.add('bg1-img');
 
  }

  ngOnInit() {
    sessionStorage.clear();
   // sessionStorage.setItem("data",'sumit');

   //localStorage--data permantely store
    document.body.classList.add('bg-img');
    
  }

  onSubmit(f){
    console.log(f.value);
    console.log(f.value.email);
    console.log(f.value.password);


    this.service.checkUser(f.value).subscribe((response)=>{
      console.log(response);
      this.msg=response;

      if(this.msg.msg!="invalid user" ){
         console.log("Validate Success..");
        
        sessionStorage.setItem("data",this.msg.user.firstName);
        this.routes.navigate(['/home']);
   
       }else{
        // alert("Incorrect Username & Password");
        this.incorrect='Incorrect Username & Password';
       }
    },(error)=>{
      if(error.status>400 && error.status<=499){
        
        this.toastr.error("Server not available");
        
      }else if(error.status==0){
       
        this.toastr.error("Server not available");
      }
     });

  }
}
