import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UserHttpService {
  suspendUser(idTodelete: any) {
    throw new Error('Method not implemented.');
  }
  url2:string="http://localhost:8089/api/users";
  url3:string="http://localhost:8089/api/login";
  url4:string="http://localhost:8089/api/users/suspend";
  url5:string="http://localhost:8089/api/users/activate";

  constructor(private httpservice:HttpClient) { 

  }

  getAllUsers(){
    return (this.httpservice.get(this.url2));
  }
  addUser(user){
    return (this.httpservice.post(this.url2,user));
  }
  updateUser(user){
    console.log("User Updated succesfully");
    return (this.httpservice.put(this.url2+"/"+user.id,user));

  }
  deleteUser(user){
    console.log("User Deleted Succesfully with id:"+user);
    return (this.httpservice.delete(this.url2+"/"+user));
  }
  SuspendUser(id){
    return (this.httpservice.get(this.url4+"/"+id));

  }
  activate(id){
    return (this.httpservice.get(this.url5+"/"+id));

  }
  

  checkUser(user){
    return (this.httpservice.get
      
      (this.url3+"/"+user.email+"/"+user.password));
  }

  getAllCountries(){

    return (this.httpservice.get("http://localhost:8089/api/users/country/getall"));
 
   }
 
}

