import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DialogService } from '../shared/dialog.service';
import { UserHttpService } from '../user-http.service';
import { ToastrService  } from 'ngx-toastr';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  nameSearch:string='';
  users:any=[];
  countries:any=[];
  country:any;
  selectedvalue: any;
  Id: any;
  FirstName: any;
  LastName: any;
  email: any;
  phoneno: any;
  stat: any;
  CreatedBy: any;
  Dep: any;
  UpdatedBy: any;
  updatevalue: any;
  myid: number;
  cid: any;
  name: string;
  isRadioClick: any;
  user: any;
  updateObj:any;
  cname: any;
  idTodelete: any;
  

  constructor(private service:UserHttpService,private routes:Router,
    private dialogservice:DialogService,
    private toastr:ToastrService) { }
  

  ngOnInit() {
    document.body.classList.add('bg1-img');
    this.GetAllUsersAndCountries();
   this.name= sessionStorage.getItem("data");
  }
  GetAllUsersAndCountries(){
    this.service.getAllUsers().subscribe((response)=>{
      console.log(response);
      this.users=response;
      
    },(error)=>{
      if(error.status>400 && error.status<=499){
        
        this.toastr.error("Server not available");
        
      }else if(error.status==0){
       
        this.toastr.error("Server not available");
      }
     });
     
    this.service.getAllCountries().subscribe((response)=>{
      console.log(response);
      this.countries=response;
    },(error)=>{
      if(error.status>400 && error.status<=499){
        
        this.toastr.error("Server not available");
        
      }else if(error.status==0){
       
        this.toastr.error("Server not available");
      }
  
  });

  }

  onAddEmpolyee(){
    this.routes.navigate(['/add']);
  }

  onUpdate(){
    if(this.radioButtonValidation()){
      //Update navigate update compo 
      console.log(this.updateObj);
      this.routes.navigate(['/update',this.cname,this.cid],{queryParams:this.updateObj});
    }else{

    }
  }
 
  onSuspend(){
    if(this.radioButtonValidation()){
      console.log("Radio Button is checked.");
      //Delete Api Call
      this.dialogservice.OpenConfirmDialog('Are you sure to suspend this record?')
      .afterClosed()
      .subscribe(res=>{
        console.log(res);
        if(res){
          //here call the delete the service
          this.service.SuspendUser(this.idTodelete).subscribe((response)=>{
            console.log("user is Suspend with id : "+this.idTodelete);
             this.GetAllUsersAndCountries();
              //in delete service subscribe method you have to call toastr
            this.toastr.success('Suspended Successfully!!! ');
          }) 
        }
      })
      
    }else
    {
      console.log("Radio Button is Unchecked.");
    }

  }
  onActivate(){
    if(this.radioButtonValidation()){
      console.log("Radio Button is checked.");
      //Delete Api Call
      this.dialogservice.OpenConfirmDialog('Are you sure to activate this record?')
      .afterClosed()
      .subscribe(res=>{
        console.log(res);
        if(res){
          //here call the delete the service
          this.service.activate(this.idTodelete).subscribe((response)=>{
            console.log("user is Suspend with id : "+this.idTodelete);
             this.GetAllUsersAndCountries();
              //in delete service subscribe method you have to call toastr
            this.toastr.success('activate Successfully!!! ');
          }) 
        }
      })
      
    }else
    {
      console.log("Radio Button is Unchecked.");
    }


  }
onDelete(){
  if(this.radioButtonValidation()){
    console.log("Radio Button is checked.");
    //Delete Api Call
    this.dialogservice.OpenConfirmDialog('Are you sure to delete this record?')
    .afterClosed()
    .subscribe(res=>{
      console.log(res);
      if(res){
        //here call the delete the service
        this.service.deleteUser(this.idTodelete).subscribe((response)=>{
          console.log("user is deleted with id : "+this.idTodelete);
           this.GetAllUsersAndCountries();
            //in delete service subscribe method you have to call toastr
          this.toastr.success('Deleted Successfully!!! ');
        }) 
      }
    })
    
  }else
  {
    console.log("Radio Button is Unchecked.");
  }
  // this.dialogservice.OpenConfirmDialog();
}

  onRadioClick(item){
    console.log(item);
    this.isRadioClick=item.firstName;
    this.idTodelete=item.id;
  // this.isRadioClick='ghh'
  this.updateObj={
    id:item.id,
    fname:item.firstName,
    lname:item.lastName,
    dep:item.dept,
    email:item.email,
    status:item.status,
    phoneno:item.phno, 
    cid:item.country.cid,
    cname:item.country.cname,
    createddtm:item.createdtm,
    createdby:item.createdby,
    updateddtm:item.updateddtm,
    updatedby:item.updatedby
  }

  this.cid=item.country.cid;
  this.cname=item.country.cname;

  }

  radioButtonValidation(){
    if(this.isRadioClick==null){
      alert("Please Select employee ..");
     return false;
    }
    else
    {
        console.log("ok selected "+this.isRadioClick);
        return true;
    }
  }


  onLogout(){
    sessionStorage.clear();
    sessionStorage.removeItem('data');    
    this.routes.navigate(['/login']);
  }

}
