import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserHttpService } from '../user-http.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css']
})
export class AddComponent implements OnInit {

  EMAIL_PATTERN=/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  users:any;
  countries:any=[];
  img:string="./assets/am.jpg";

  constructor(private service:UserHttpService,private routes:Router) { }

  ngOnInit() {
    document.body.classList.add('bg1-img');
    this.GetAllUsersAndCountries();
  }
  GetAllUsersAndCountries(){
    this.service.getAllUsers().subscribe((response)=>{
      console.log(response);
      this.users=response;
    });
    this.service.getAllCountries().subscribe((response)=>{
      console.log(response);
      this.countries=response;
    });

  }
  onSubmit(formvalue){
    console.log(formvalue.value);
    this.users=formvalue.value;
    console.log(this.users);

    let user={
      firstName:this.users.firstname,
      lastName:this.users.lastname,
      email:this.users.email,
      phno:this.users.phone,
      status:this.users.status,
      createdby:this.users.firstname,
      createdtm:new Date(),
      dept:this.users.department,
      updatedby:this.users.firstname,
      updateddtm:new Date(),
      country: {
        cid: this.users.country.cid,
        cname: this.users.country.cname
        }

        }
        console.log(user);
    this.service.addUser(user).subscribe((response)=>{

      console.log(response);
      this.GetAllUsersAndCountries();
      this.routes.navigate(['/home']);
    })

  }

  

}
