import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserHttpService } from '../user-http.service';

@Component({
  selector: 'app-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.css']
})
export class UpdateComponent implements OnInit {

  Id:number;
  FirstName:string='';
  LastName:string='';
  phoneno:string='';
  stat:string='';
  CreatedBy:string='';
  Dep:string='';
  UpdatedBy:string='';
  email:string='';
  selectedvalue:any;
  updatevalue:any;

  users:any=[];
  countries:any=[];
  country:any;

  constructor(private router:ActivatedRoute,private routes:Router,private service:UserHttpService) { }


  ngOnInit() {
    this.GetAllUsersAndCountries();

    this.router.paramMap.subscribe((param)=>{
      this.updatevalue=param.get("cname");
      this.selectedvalue=+param.get("cid");
       console.log(this.updatevalue);
       console.log(this.selectedvalue);

    })
  

    this.router.queryParamMap.subscribe ((query)=>{
       console.log(query.keys)
       console.log("Url Data is ::"+query.get('id'));
      console.log("Url Data is ::"+query.get('fname'));
      // console.log("Url Data is ::"+query.get('lastName'));
       console.log("Url Data is ::"+query.get('email'));

      this.Id=+query.get('id');
      this.FirstName=query.get('fname');
      this.LastName=query.get('lname');
      this.email=query.get('email');
      this.phoneno=query.get("phoneno");
      this.stat=query.get("status");
      this.Dep=query.get("dep");
      this.CreatedBy=query.get("createdby");
      this.UpdatedBy=query.get("updatedby");

    })

  }

  changeCountry(){
    console.log(this.updatevalue);
    console.log(this.countries);
 
  }
  onFinalUpdateUser(){

    let user={
      id:this.Id,
      firstName:this.FirstName,
      lastName:this.LastName,
      email:this.email,
      phno:this.phoneno,
      status:this.stat,
      createdby:this.CreatedBy,
      createdtm:new Date(),
      dept:this.Dep,
      updatedby:this.UpdatedBy,
      updateddtm:new Date(),
      country: {
        cid:this.updatevalue.cid,
        cname: this.updatevalue.cname
        }
      
    }
    this.service.updateUser(user)
    .subscribe((response)=>{
      console.log(response);
      this.GetAllUsersAndCountries();
      this.routes.navigate(["/home"]);
    })
    
    
  }

  GetAllUsersAndCountries(){
    this.service.getAllCountries().subscribe((response)=>{
      this.countries=response;
    });
    this.service.getAllUsers().subscribe((response)=>{
      console.log(response);
      this.users=response;
    });
  }
}
